    </div>
    <!-- 註冊頁 -->
    <div class="col-lg-8 col-md-12 mx-auto" id="register-page">
      <form class="ms-2">
        <h2 class="my-4 text-start fw-bold">會員註冊</h2>
        <div class="row">
          <!-- 暱稱 -->
          <div class="form-floating col-lg-5 col-md-12 mb-3">
            <div class="text-start mb-1 ms-2 fw-bold">暱稱</div>
            <input
              type="text"
              :class="isnickname"
              v-model="nickname"
              spellcheck="false"
