          </div>
        </div>
        <div class="row">
          <!-- 密碼1 -->
          <div class="form-floating col-lg-5 col-md-12 mb-3">
            <div class="text-start mb-1 ms-2 fw-bold">密碼</div>
            <input
              type="password"
              :class="isnewpassword1"
              v-model="newpassword1"
              spellcheck="false"
              minlength="6"
              maxlength="16"
              placeholder="password"
              @input="isnewpassword1verify"
              required
            />
            <label class="mt-4">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="25"
                height="25"
                fill="currentColor"
                class="bi bi-lock ms-3 me-1"
                viewBox="0 0 16 16"
              >
                <path
                  d="M8 1a2 2 0 0 1 2 2v4H6V3a2 2 0 0 1 2-2zm3 6V3a3 3 0 0 0-6 0v4a2 2 0 0 0-2 2v5a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2V9a2 2 0 0 0-2-2zM5 8h6a1 1 0 0 1 1 1v5a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1V9a1 1 0 0 1 1-1z"
                /></svg
              >6-16個英數文字</label
            >
          </div>
          <!-- 密碼2 -->
          <div class="form-floating col-lg-5 col-md-12 mb-2">
            <div class="text-start mb-1 ms-2 fw-bold">確認密碼</div>
            <input
              type="password"
              :class="isnewpassword2"
              v-model="newpassword2"
              spellcheck="false"
              minlength="6"
              maxlength="16"
              placeholder="password"
              @input="isnewpassword2verify"
              required
            />
            <label class="fw-bold mt-4">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="25"
                height="25"
                fill="currentColor"
                class="bi bi-lock ms-3 me-1"
                viewBox="0 0 16 16"
              >
                <path
                  d="M8 1a2 2 0 0 1 2 2v4H6V3a2 2 0 0 1 2-2zm3 6V3a3 3 0 0 0-6 0v4a2 2 0 0 0-2 2v5a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2V9a2 2 0 0 0-2-2zM5 8h6a1 1 0 0 1 1 1v5a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1V9a1 1 0 0 1 1-1z"
                /></svg
            ></label>
          </div>
        </div>
        <!-- 送出 -->
        <button
          class="btn btn-primary col-4 mt-3 me-2 rounded-pill"
          @click="RegisterSend"
