    </div>
    <!-- 註冊頁結束 -->
  </div>
  <LoaadView v-if="loading" loadtext="載入中..." />
</template>

<script>
import Loading from "@/components/Loading.vue";
import { toRefs, reactive, onMounted } from "vue";
import { useRouter, onBeforeRouteLeave } from "vue-router";
import { useStore } from "vuex";
import { firebaseauth } from "@/config/firebasedb";
import {
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
  deleteUser,
  sendEmailVerification,
  sendPasswordResetEmail,
  onAuthStateChanged,
} from "firebase/auth";
import CryptoJS from "crypto-js";
import axios from "axios";

export default {
  name: "Login",
  components: {
    LoaadView: Loading,
  },
  setup() {
    //api
    const store = useStore();
    //路由跳轉
    const router = useRouter();
    //信箱格式
    const emailPattern = /^[\w-#//\\]*@+[\w.]+(.[A-Za-z]){0,2}$/;
    // ^? 頭到尾，\w為[A-Za-z0-9_]，()群組，-#?!可直接打\/要多打條重複才跳脫，*為0到多次，+為一次以上，?0或1次，{}為自訂次數
    //密碼格式
    const passwordPattern = /^[A-Za-z0-9]{6,16}$/;
    //資料驗證
    const dataclass = reactive({
      isemail: `form-control border rounded-pill ps-5`,
      ispassword: `form-control border rounded-pill ps-5`,
      isnickname: `form-control border rounded-pill ps-4 mx-0`,
      isnewemail: `form-control border rounded-pill ps-4 mx-0`,
      isnewpassword1: `form-control border rounded-pill ps-4 mx-0`,
      isnewpassword2: `form-control border rounded-pill ps-4 mx-0`,
    });
    //資料儲存
    const data = reactive({
      email: ``,
      password: ``,
      checked: false,
      nickname: ``,
      newemail: ``,
      newpassword1: ``,
      newpassword2: ``,
      userinfo: [],
      loading: false,
      starttime: ``, //儲存發送驗證碼的setTimeout，用來防止動作繼續執行
      accountfinished: true,
    });
    // email資料驗證，動態改變class
    function isemailverify() {
      dataclass.isemail = `form-control border rounded-pill ps-5`;
      if (data.email.length > 0) {
        dataclass.isemail = `form-control is-invalid border rounded-pill ps-5`;
        if (emailPattern.test(data.email) == true && data.email.length > 4) {
          dataclass.isemail = `form-control is-valid border rounded-pill ps-5`;
        }
      }
    }
    // password資料驗證，動態改變class
    function ispasswordverify() {
      dataclass.ispassword = `form-control border rounded-pill ps-5`;
      if (data.password.length > 0) {
        dataclass.ispassword = `form-control is-invalid border rounded-pill ps-5`;
        if (passwordPattern.test(data.password) == true) {
          dataclass.ispassword = `form-control is-valid border rounded-pill ps-5`;
        }
      }
    }
    // nickname資料驗證，動態改變class
    function isnicknameverify() {
      dataclass.isnickname = `form-control border rounded-pill ps-4 mx-0`;
      if (data.nickname.length > 0) {
        dataclass.isnickname = `form-control is-valid border rounded-pill ps-4 mx-0`;
      }
    }
    // newemail資料驗證，動態改變class
    function isnewemailverify() {
      dataclass.isnewemail = `form-control border rounded-pill ps-4 mx-0`;
      if (data.newemail.length > 0) {
        dataclass.isnewemail = `form-control is-invalid border rounded-pill ps-4 mx-0`;
        if (
          emailPattern.test(data.newemail) == true &&
          data.newemail.length > 4
        ) {
          dataclass.isnewemail = `form-control is-valid border rounded-pill ps-4 mx-0`;
        }
      }
    }
    // newpassword1資料驗證，動態改變class
    function isnewpassword1verify() {
      dataclass.isnewpassword1 = `form-control border rounded-pill ps-4 mx-0`;
      if (data.newpassword1.length > 0) {
        dataclass.isnewpassword1 = `form-control is-invalid border rounded-pill ps-4 mx-0`;
        if (passwordPattern.test(data.newpassword1) == true) {
          dataclass.isnewpassword1 = `form-control is-valid border rounded-pill ps-4 mx-0`;
          if (data.newpassword1 === data.newpassword2) {
            dataclass.isnewpassword2 = `form-control is-valid border rounded-pill ps-4 mx-0`;
          } else {
            dataclass.isnewpassword2 = `form-control is-invalid border rounded-pill ps-4 mx-0`;
          }
        }
      } else if (
        data.newpassword2.length == 0 &&
        data.newpassword1.length == 0
      ) {
        dataclass.isnewpassword2 = `form-control border rounded-pill ps-4 mx-0`;
      }
    }
    // newpassword2資料驗證，動態改變class
    function isnewpassword2verify() {
      dataclass.isnewpassword2 = `form-control border rounded-pill ps-4 mx-0`;
      if (data.newpassword2.length > 0) {
        dataclass.isnewpassword2 = `form-control is-invalid border rounded-pill ps-4 mx-0`;
        if (
          passwordPattern.test(data.newpassword2) == true &&
          data.newpassword1 === data.newpassword2
        ) {
          dataclass.isnewpassword2 = `form-control is-valid border rounded-pill ps-4 mx-0`;
        }
      }
    }
    // 初始化設定
    function InitData() {
      if (!localStorage.getItem(`usermemory`)) {
        data.email = ``; //信箱清空
        data.password = ``; //密碼清空
      }
      data.nickname = ``; //暱稱清空
      data.newemail = ``; //信箱清空
      data.newpassword1 = ``; //密碼清空
      data.newpassword2 = ``; //暱稱清空
      data.userinfo = []; //資料清空
      dataclass.isemail = `form-control border rounded-pill ps-5`;
      dataclass.ispassword = `form-control border rounded-pill ps-5`;
      dataclass.isnickname = `form-control border rounded-pill ps-4 mx-0`;
      dataclass.isnewemail = `form-control border rounded-pill ps-4 mx-0`;
      dataclass.isnewpassword1 = `form-control border rounded-pill ps-4 mx-0`;
      dataclass.isnewpassword2 = `form-control border rounded-pill ps-4 mx-0`;
    }
    // 信箱初始化
    function InitEmail() {
      if (!localStorage.getItem(`usermemory`)) {
        data.email = ``; //信箱清空
        data.password = ``; //密碼清空
      }
      data.newemail = ``; //信箱清空
      data.userinfo = []; //資料清空
      dataclass.isemail = `form-control border rounded-pill ps-5`;
      dataclass.ispassword = `form-control border rounded-pill ps-5`;
      dataclass.isnewemail = `form-control border rounded-pill ps-4 mx-0`;
    }
    // 密碼初始化
    function InitPassword() {
      data.password = ``; //密碼清空
      data.nickname = ``; //暱稱清空
      data.newemail = ``; //信箱清空
      data.newpassword1 = ``; //密碼清空
      data.newpassword2 = ``; //暱稱清空
      data.userinfo = []; //資料清空
      dataclass.ispassword = `form-control border rounded-pill ps-5`;
      dataclass.isnickname = `form-control border rounded-pill ps-4 mx-0`;
      dataclass.isnewemail = `form-control border rounded-pill ps-4 mx-0`;
      dataclass.isnewpassword1 = `form-control border rounded-pill ps-4 mx-0`;
      dataclass.isnewpassword2 = `form-control border rounded-pill ps-4 mx-0`;
    }
    // Alert訊息
    function AlertMessage(value) {
      // eslint-disable-next-line no-undef
      const Toast = Swal.mixin({
        toast: true,
        position: "center",
        showConfirmButton: false,
        timer: 1500,
        timerProgressBar: true,
        didOpen: (toast) => {
          // eslint-disable-next-line no-undef
          toast.addEventListener("mouseenter", Swal.stopTimer);
          // eslint-disable-next-line no-undef
          toast.addEventListener("mouseleave", Swal.resumeTimer);
        },
      });

      Toast.fire({
        icon: "error",
        title: value,
      });
    }
    // 驗證碼警告視窗
    function VerifiedAlert() {
      let timerInterval, time;
      // eslint-disable-next-line no-undef
      const swalWithBootstrapButtons = Swal.mixin({
        customClass: {
          denyButton: "btn btn-success",
          footer: "btn",
        },
        buttonsStyling: false,
      });
      // eslint-disable-next-line no-undef
      swalWithBootstrapButtons
        .fire({
          title: `<p class="fw-bolder">請完成信箱驗證</p>`,
          html: `<div><b class="me-1 text-danger"></b><span class="ms-1 fw-bold">秒後自動關閉</span></div>`,
          allowOutsideClick: false,
          showCloseButton: true,
          showDenyButton: true,
          denyButtonText: "已驗證",
          position: "center",
          footer: `<div>
          <div class="col-12">
          <span class="fw-bold me-2">未收到驗證碼?</span>
          <span id="re-verified" class="fw-bolder text-info">重發驗證碼</span>
          </div>
          <div id="delete-account" class="col-12 fs-5 fw-bolder text-danger">
          刪除帳號
          </div>
          </div>`,
          timer: 61000,
          timerProgressBar: true,
          didOpen: () => {
            // eslint-disable-next-line no-undef
            Swal.showLoading();
            time = 60;
            // eslint-disable-next-line no-undef
            const b = Swal.getHtmlContainer().querySelector("b");
            timerInterval = setInterval(() => {
              // eslint-disable-next-line no-undef
              // b.textContent = Swal.getTimerLeft();
              b.textContent = time--;
            }, 1000);
          },
          willClose: () => {
            clearInterval(timerInterval);
          },
        })
        .then((result) => {
          /* Read more about handling dismissals below */
          if (result.isDenied) {
            let tempuser = JSON.parse(localStorage.getItem(`tempuser`));
            tempuser[0].emailverification = true;
            SaveLocalStorage(`tempuser`, tempuser);
            data.accountfinished == true;
            window.location.reload();
          } else if (result.dismiss == "close") {
            clearTimeout(data.starttime);
            firebaseDelete();
            DeleteLocalStorage(`tempuser`);
          }
        });
      document.getElementById("re-verified").onclick = function () {
        clearTimeout(data.starttime);
        firebaseSendVerified();
      };
      document.getElementById("delete-account").onclick = function () {
        let swalclose = document.getElementsByClassName("swal2-close");
        for (let i = 0; i < swalclose.length; i++) {
          swalclose[i].click();
        }
      };
    }
    // localStorage儲存
    function SaveLocalStorage(localstoragename, localstoragedata) {
      localStorage.setItem(localstoragename, JSON.stringify(localstoragedata));
    }
    // localStorage刪除
    function DeleteLocalStorage(localstoragename) {
      if (localStorage.getItem(localstoragename)) {
        localStorage.removeItem(localstoragename);
      }
    }
    //取得資料
    function GetData(apiparams) {
      axios({
        method: "post",
        url: store.state.api.GoogleAPI,
        params: apiparams,
      })
        .then((responses) => {
          if (responses.status === 200) {
            let response = responses.data;
            //註冊回應
            if (apiparams["task"] == "1") {
              //註冊成功
              if (response[0]["status"] == "0") {
                data.checked == false;
                DeleteLocalStorage(`usermemory`);
                // 加密
                let now = new Date().getTime(); //記錄登入時間
                let storetime = now.toString();
                let encryptSemail = CryptoJS.AES.encrypt(
                  apiparams["email"],
                  storetime
                ).toString();
                let encryptEemail = CryptoJS.AES.encrypt(
                  encryptSemail,
                  response[0]["token"]
                ).toString();
                let encryptstoretime = CryptoJS.AES.encrypt(
                  storetime,
                  response[0]["token"]
                ).toString();
                //
                data.userinfo.push({
                  nickname: apiparams["nickname"],
                  email: encryptEemail,
                  level: "1",
                  token: response[0]["token"],
                  avatar: "",
                  storetime: encryptstoretime,
                });
                SaveLocalStorage(`user`, data.userinfo);
                data.loading = false;
                InitData();
                router.replace({ name: "Information" });
                //註冊失敗
              } else if (response[0]["status"] == "1") {
                data.loading = false;
                InitEmail();
                AlertMessage("此信箱已被使用!!");
                return;
              } //登入回應
            } else if (apiparams["task"] == "2") {
              //登入成功
              if (response[0]["status"] == "0") {
                // 加密
                let now = new Date().getTime(); //記錄登入時間
                let storetime = now.toString();
                let encryptSemail = CryptoJS.AES.encrypt(
                  apiparams["email"],
                  storetime
                ).toString();
                let encryptEemail = CryptoJS.AES.encrypt(
                  encryptSemail,
                  response[0]["token"]
                ).toString();
                let encryptstoretime = CryptoJS.AES.encrypt(
                  storetime,
                  response[0]["token"]
                ).toString();
                //
                data.userinfo.push({
                  nickname: response[0]["nickname"],
                  email: encryptEemail,
                  level: response[0]["level"],
                  token: response[0]["token"],
                  avatar: response[0]["avatar"],
                  storetime: encryptstoretime,
                });
                SaveLocalStorage(`user`, data.userinfo);
                store.dispatch("auth/LoginTask", data.userinfo);
                data.loading = false;
                InitData();
                router.replace({ name: "Information" });
                //登入失敗
              } else if (response[0]["status"] == "1") {
                let params = {
                  email: apiparams["email"],
                  password: apiparams["password"],
                  task: "5",
                };
                GetData(params);
              }
              //改密碼回應
            } else if (apiparams["task"] == "5") {
              //改密碼成功
              if (response[0]["status"] == "0") {
                let params = {
                  email: apiparams["email"],
                  password: apiparams["password"],
                  task: "2",
                };
                GetData(params);
                data.loading = false;
                //改密碼失敗
              } else if (response[0]["status"] == "1") {
                DeleteLocalStorage(`usermemory`);
                data.loading = false;
                AlertMessage("未註冊帳號!!");
              }
            } else if (apiparams["task"] == "6") {
              if (response[0]["status"] == "0") {
                sendPasswordResetEmail(firebaseauth, apiparams["email"])
                  .then(() => {
                    // eslint-disable-next-line no-undef
                    Swal.fire("請前往信箱修改", "", "success");
                  })
                  .catch(() => {
                    AlertMessage("可能沒有此帳號");
                  });
                data.loading = false;
              } else if (response[0]["status"] == "1") {
                AlertMessage("可能沒有此帳號");
                data.loading = false;
              }
            }
          } else {
            console.log("api回應失敗");
          }
        })
        .catch(() => {
          console.log("api呼叫失敗");
        });
    }
    //登入送出
    function LoginSend() {
      if (data.email == `` || data.password == ``) {
        AlertMessage("帳密不能為空");
        return;
      }
      if (emailPattern.test(data.email) == false) {
        AlertMessage("信箱格式錯誤");
        return;
      }
      if (passwordPattern.test(data.password) == false) {
        AlertMessage("密碼格式錯誤");
        return;
      }
      firebaseLogin();
    }
    // 忘記密碼
    async function ForgetPassword() {
      // eslint-disable-next-line no-undef
      const { value: email } = await Swal.fire({
        title: "請輸入您的email",
        input: "email",
        inputPlaceholder: "例如:XXXX@gmail.com",
        showCancelButton: true,
        confirmButtonText: "確認",
        cancelButtonText: "取消",
      });

      if (email) {
        let params = {
          email: email,
          task: "6",
        };
        data.loading = true;
        GetData(params);
      }
    }
    //註冊送出
    function RegisterSend() {
      if (
        data.nickname == `` ||
        data.newemail == `` ||
        data.newpassword1 == `` ||
        data.newpassword2 == ``
      ) {
        AlertMessage("註冊輸入不能為空");
        return;
      }
      if (emailPattern.test(data.newemail) == false) {
        AlertMessage("信箱格式錯誤");
        return;
      }
      if (
        passwordPattern.test(data.newpassword1) == false ||
        passwordPattern.test(data.newpassword2) == false
      ) {
        AlertMessage("密碼格式錯誤");
        return;
      }
      if (data.newpassword1 !== data.newpassword2) {
        AlertMessage("密碼確認不同");
        return;
      }
      firebaseRegister();
    }
    // firebase註冊
    function firebaseRegister() {
      createUserWithEmailAndPassword(
        firebaseauth,
        data.newemail,
        data.newpassword2
      )
        .then(() => {
          data.userinfo.push({
            nickname: data.nickname,
            email: data.newemail,
            password: data.newpassword2,
            emailverification: false,
          });
          SaveLocalStorage(`tempuser`, data.userinfo);
          firebaseSendVerified();
          InitData();
        })
        .catch((error) => {
          //錯誤訊息
          if (error.code == "auth/email-already-in-use") {
            AlertMessage("此信箱已被使用");
          } else if (error.code == "auth/invalid-email") {
            AlertMessage("此信箱無法接收驗證碼");
          } else {
            console.log("註冊出錯");
          }
          InitEmail();
        });
    }
    // firebase登入
    function firebaseLogin() {
      let passwordparams = ``;
      if (localStorage.getItem(`usermemory`)) {
        let usermemory = JSON.parse(localStorage.getItem(`usermemory`));
        // 解密
        let decryptpassword = CryptoJS.AES.decrypt(
          usermemory[0].password,
          "encryptyourpassword"
        );
        let password = decryptpassword.toString(CryptoJS.enc.Utf8);
        //
        if (data.password == password) {
          passwordparams = data.password;
        } else {
          passwordparams = password;
        }
      } else {
        passwordparams = data.password;
      }
      signInWithEmailAndPassword(firebaseauth, data.email, passwordparams)
        .then(() => {
          if (data.checked == true) {
            //判斷有無紀錄帳密
            if (localStorage.getItem(`usermemory`)) {
              let usermemory = JSON.parse(localStorage.getItem(`usermemory`));
              // 解密
              let decryptemail = CryptoJS.AES.decrypt(
                usermemory[0].email,
                "encryptyouremail"
              );
              let email = decryptemail.toString(CryptoJS.enc.Utf8);
              let decryptpassword = CryptoJS.AES.decrypt(
                usermemory[0].password,
                "encryptyourpassword"
              );
              let password = decryptpassword.toString(CryptoJS.enc.Utf8);
              //
              if (data.email !== email || passwordparams !== password) {
                // 加密
                let encryptpassword = CryptoJS.AES.encrypt(
                  passwordparams,
                  "encryptyourpassword"
                ).toString();
                let encryptemail = CryptoJS.AES.encrypt(
                  data.email,
                  "encryptyouremail"
                ).toString();
                //
                data.userinfo.push({
                  email: encryptemail,
                  password: encryptpassword,
                });
                SaveLocalStorage(`usermemory`, data.userinfo);
              }
            } else {
              // 加密
              let encryptpassword = CryptoJS.AES.encrypt(
                passwordparams,
                "encryptyourpassword"
              ).toString();
              let encryptemail = CryptoJS.AES.encrypt(
                data.email,
                "encryptyouremail"
              ).toString();
              //
              data.userinfo.push({
                email: encryptemail,
                password: encryptpassword,
              });
              SaveLocalStorage(`usermemory`, data.userinfo);
            }
          } else {
            DeleteLocalStorage(`usermemory`);
          }
          //信箱已驗證
          if (firebaseauth.currentUser.emailVerified == true) {
            let params = {
              email: data.email,
              password: passwordparams,
              task: "2",
            };
            data.loading = true;
            GetData(params);
            InitData();
          } //信箱未驗證
          else if (firebaseauth.currentUser.emailVerified == false) {
            firebaseSendVerified();
          }
        })
        .catch((error) => {
          DeleteLocalStorage(`usermemory`);
          //錯誤訊息
          if (error.code == "auth/invalid-email") {
            AlertMessage("此信箱無效");
          } else if (error.code == "auth/user-not-found") {
            AlertMessage("此信箱尚未註冊");
          } else if (error.code == "auth/wrong-password") {
            AlertMessage("密碼輸入錯誤");
            InitPassword();
            return;
          } else {
            console.log("登入出錯");
          }
          InitData();
        });
    }
    // firebase刪除帳號
    async function firebaseDelete() {
      data.accountfinished = true;
      if (localStorage.getItem(`tempuser`)) {
        let tempuser = JSON.parse(localStorage.getItem(`tempuser`));
        if (firebaseauth.currentUser.email !== tempuser[0].email) {
          signInWithEmailAndPassword(
            firebaseauth,
            tempuser[0].email,
            tempuser[0].password
          );
        }
      }
      deleteUser(firebaseauth.currentUser)
        .then(() => {
          console.log("刪除成功");
        })
        .catch(() => {
          let userstatus = firebaseauth.currentUser;
          if (userstatus !== ``) {
            firebaseDelete();
          } else {
            console.log("刪除失敗");
          }
        });
    }
    // firebase寄送信箱驗證
    function firebaseSendVerified() {
      firebaseauth.languageCode = `zh-TW`;
      sendEmailVerification(firebaseauth.currentUser)
        .then(() => {
          VerifiedAlert();
          data.starttime = setTimeout(() => {
            data.accountfinished = true;
            window.location.reload();
          }, 61000);
        })
        .catch(() => {
          console.log("驗證碼發送次數過多");
          VerifiedAlert();
          data.starttime = setTimeout(() => {
            data.accountfinished = true;
            window.location.reload();
          }, 61000);
        });
    }
    // firebase登入和信箱驗證狀態
    function firebaseState() {
      //判斷有無帳號資料，並且存有暫存資料則刪除
      if (localStorage.getItem(`user`)) {
        DeleteLocalStorage(`tempuser`);
        router.replace({ name: "Information" });
      } else {
        let userstatus = firebaseauth.currentUser;
        //判斷是否有帳號登入資料
        if (userstatus !== ``) {
          onAuthStateChanged(firebaseauth, (user) => {
            // 信箱驗證成功
            if (user.emailVerified == true) {
              data.accountfinished = true;
              let tempuser = JSON.parse(localStorage.getItem(`tempuser`));
              //api參數
              let params = {
                nickname: tempuser[0].nickname,
                email: tempuser[0].email,
                password: tempuser[0].password,
                task: "1",
              };
              localStorage.removeItem(`tempuser`);
              data.loading = true;
              //api呼叫
              GetData(params);
            } //信箱驗證失敗
            else if (user.emailVerified == false) {
              data.accountfinished = false;
            }
          });
        }
      }
    }
    onMounted(() => {
      if (localStorage.getItem(`user`)) {
        store.dispatch("auth/SignoutTask");
      }
      if (data.accountfinished == true) {
        if (localStorage.getItem(`tempuser`)) {
          let tempuser = JSON.parse(localStorage.getItem(`tempuser`));
          let emailverification = tempuser[0].emailverification;
          if (emailverification == false) {
            let email = tempuser[0].email;
            // eslint-disable-next-line no-undef
            Swal.fire({
              title: "尚有未創建成功之帳號",
              html:
                `<div class="fw-bolder text-secondary">信箱:` +
                email +
                `</div>`,
              allowOutsideClick: false,
              confirmButtonText: "繼續完成",
              showDenyButton: true,
              denyButtonText: `重創帳號`,
              confirmButtonColor: "#3085d6",
              denyButtonColor: "#d33",
            }).then((result) => {
              if (result.isConfirmed) {
                VerifiedAlert();
                data.starttime = setTimeout(() => {
                  window.location.reload();
                }, 61000);
              } else if (result.isDenied) {
                firebaseDelete();
                DeleteLocalStorage(`tempuser`);
              }
            });
          }
        }
      }
      if (localStorage.getItem(`usermemory`)) {
        data.checked = true;
        let usermemory = JSON.parse(localStorage.getItem(`usermemory`));
        // 解密
        let decryptemail = CryptoJS.AES.decrypt(
          usermemory[0].email,
          "encryptyouremail"
        );
        let email = decryptemail.toString(CryptoJS.enc.Utf8);
        data.email = email;
        let decryptpassword = CryptoJS.AES.decrypt(
          usermemory[0].password,
          "encryptyourpassword"
        );
        let password = decryptpassword.toString(CryptoJS.enc.Utf8);
        //
        const str =
          "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghjklmnopqrstuvwxyz0123456789";
        let randomstr = "";
        for (let i = password.length; i > 0; --i) {
          randomstr += str[Math.floor(Math.random() * str.length)];
        }
        data.password = randomstr;
      }
      firebaseState();
    });
    // eslint-disable-next-line no-unused-vars
    onBeforeRouteLeave((to, from, next) => {
      if (data.accountfinished == false) {
        // eslint-disable-next-line no-undef
        Swal.fire({
          title: "離開當前面",
          html: `<div class="fw-bolder text-secondary">創建帳號未完成時不會儲存資料</div>`,
          allowOutsideClick: false,
          confirmButtonText: "確認",
          showDenyButton: true,
          denyButtonText: `繼續完成`,
          confirmButtonColor: "#3085d6",
          denyButtonColor: "#d33",
        }).then((result) => {
          if (result.isConfirmed) {
            clearTimeout(data.starttime);
            firebaseDelete();
            DeleteLocalStorage(`tempuser`);
            next();
          } else if (result.isDenied) {
            VerifiedAlert();
            data.starttime = setTimeout(() => {
              window.location.reload();
            }, 61000);
            return false;
          }
        });
        return;
      }
      next();
    });
    return {
      ...toRefs(dataclass),
      ...toRefs(data),
      isemailverify,
      ispasswordverify,
      isnicknameverify,
      isnewemailverify,
      isnewpassword1verify,
      isnewpassword2verify, //is開頭為input驗證
      InitData,
      InitEmail,
      InitPassword,
      AlertMessage,
      VerifiedAlert,
      SaveLocalStorage,
      DeleteLocalStorage,
      LoginSend,
      ForgetPassword,
      RegisterSend,
      GetData,
      firebaseRegister,
      firebaseLogin,
      firebaseDelete,
      firebaseSendVerified,
      firebaseState,
    };
  },
};
</script>

