<template>
  <div class="container-fluid col-lg-6 col-md-8 mx-auto mt-3">
    <div class="py-3 fw-bloder fs-1" id="home-title">
      以RSS資料，來實現站內搜尋功能
    </div>
    <div class="mt-3">
      <input
        type="text"
        class="form-control mx-auto mb-5"
        maxlength="15"
        v-model="search"
        spellcheck="false"
        placeholder="搜尋內容"
        @input="FilterSearch"
        autofocus
      />
      <div
        class="d-inline-flex col-md-6 mb-5"
        v-for="(news, index) in FilterSearch()"
        :key="news"
      >
        <a
          :href="news.link"
          class="mx-auto mt-1 text-decoration-none border border-3"
          id="card-show"
        >
          <div class="mx-auto mt-1" style="width: 18rem">
            <img :src="news.thumbnail" id="card-show-image" />
            <div class="text-dark">
              <div v-if="search.length > 0">
                <p v-html="searchnews[index]" class="text-start fs-5"></p>
              </div>
              <div v-else>
                <p class="text-start fs-5">{{ news.title }}</p>
              </div>
              <p class="text-dark">
                {{ news.pubDate }}
              </p>
            </div>
          </div>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
import { toRefs, reactive, onMounted } from "vue";
import axios from "axios";
import { useStore } from "vuex";

export default {
  name: "Home",
  setup() {
    const store = useStore();
    const data = reactive({
      search: ``,
      allnews: [], //新聞所有資料
      searchnews: [], //搜尋新聞
      filtertext: ``,
    });
    // 連api
    function GetData() {
      axios
        .get(
          "https://api.rss2json.com/v1/api.json?rss_url=https%3A%2F%2Fcn.nytimes.com%2Frss%2Fzh-hant%2F"
        )
        .then((response) => {
          for (let i = 0; i < response["data"]["items"].length; i++) {
            data.allnews.push({
              title: response["data"]["items"][i]["title"],
              pubDate: response["data"]["items"][i]["pubDate"],
              link: response["data"]["items"][i]["link"],
              thumbnail: response["data"]["items"][i]["thumbnail"],
            });
          }
        });
    }
    function FilterSearch() {
      if (data.search.length > 0) {
        data.searchnews = data.allnews
          .filter((searchResult) => searchResult["title"].match(data.search))
          .map((searchResult) =>
            searchResult["title"].replace(
              data.search,
              "<span class='search-text'>" + data.search + "</span>"
            )
          );
        return data.allnews.filter((searchResult) =>
          searchResult["title"].match(data.search)
        );
      } else {
        return data.allnews;
      }
    }
    onMounted(() => {
      setInterval(GetData(), 24 * 60 * 60 * 1000);
      FilterSearch();
      if (localStorage.getItem(`user`)) {
        let user = JSON.parse(localStorage.getItem(`user`));
        store.dispatch("auth/LoginTask", user[0]);
      } else {
        store.dispatch("auth/SignoutTask");
      }
    });
    return {
      ...toRefs(data),
      GetData,
      FilterSearch,
    };
  },
};
</script>

<style lang="scss">
#home-title {
  height: 10%;
  background-color: rgb(119, 178, 255);
}
#card-show {
  width: 19em;
  height: 18em;
  #card-show-image {
    width: 18em;
    height: 10em;
  }
}
.search-text {
  background-color: red;
  color: white;
}
</style>
