//儲存的資料
export const state = {
  user: {
    path: `/login`,
    pathname: `登入/註冊`,
    usercheck: false,
    avatarcheck: false,
    userlevel: ``,
    useravatar: ``,
    usernickname: ``,
  },
};

//改變state資料，只處理同步動作
export const mutations = {
  Login(state, data) {
    state.user.path = `/information`;
    state.user.pathname = `用戶設置`;
    state.user.usercheck = true;
    if (data.avatar !== ``) {
      let image = new Image();
      image.onload = () => {
        if (image.width > 0 || image.height > 0) {
          state.user.avatarcheck = true;
        }
      };
      image.src = data.avatar;
    } else {
      state.user.avatarcheck = false;
    }
    state.user.useravatar = data.avatar;
    state.user.userlevel = data.level;
    state.user.usernickname = data.nickname;
  },
  Signout(state) {
    state.user.path = `/login`;
    state.user.pathname = `登入/註冊`;
    state.user.usercheck = false;
    state.user.avatarcheck = false;
    state.user.userlevel = ``;
    state.user.useravatar = ``;
    state.user.usernickname = ``;
  },
};

//相當於methods
export const actions = {
  LoginTask(event, data) {
    event.commit(`Login`, data);
  },
  SignoutTask(event) {
    event.commit(`Signout`);
  },
};

//相當於computed
export const getters = {};

export default {
  state,
  mutations,
  actions,
  getters,
  namespaced: true,
};
