<template>
  <NavbarView />
  <OffcanvasView
    :path="path"
    :pathname="pathname"
    :usercheck="usercheck"
    :avatarcheck="avatarcheck"
    :userlevel="userlevel"
    :useravatar="useravatar"
    :usernickname="usernickname"
  />
  <div class="mb-5"><router-view /></div>
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import Offcanvas from "@/components/Offcanvas.vue";
import { toRefs, reactive, onMounted, computed } from "vue";
import { useStore } from "vuex";

export default {
  name: "App",
  components: {
    NavbarView: Navbar,
    OffcanvasView: Offcanvas,
  },
  setup() {
    const store = useStore();
    const data = reactive({
      path: computed(() => {
        let path = store.state.auth.user.path;
        return path;
      }),
      pathname: computed(() => {
        let pathname = store.state.auth.user.pathname;
        return pathname;
      }),
      usercheck: computed(() => {
        let usercheck = store.state.auth.user.usercheck;
        return usercheck;
      }),
      avatarcheck: computed(() => {
        let avatarcheck = store.state.auth.user.avatarcheck;
        return avatarcheck;
      }),
      useravatar: computed(() => {
        let useravatar = store.state.auth.user.useravatar;
        return useravatar;
      }),
      usernickname: computed(() => {
        let usernickname = store.state.auth.user.usernickname;
        return usernickname;
      }),
      userlevel: computed(() => {
        let userlevel = store.state.auth.user.userlevel;
        return userlevel;
      }),
    });
    onMounted(() => {});
    return {
      ...toRefs(data),
    };
  },
};
</script>

