import { createRouter, createWebHistory } from "vue-router";
import { useStore } from "vuex";
import { firebaseauth } from "@/config/firebasedb";
import { signOut } from "firebase/auth";
import CryptoJS from "crypto-js"; //加解密
import axios from "axios";
import Home from "../views/Home.vue";
import About from "../views/About.vue";
import Login from "../views/Pages/Login.vue";
import Information from "../views/Pages/Information.vue";
import MessageBoard from "../views/Pages/MessageBoard.vue";

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
    meta: { requiresAuth: false },
  },
  {
    path: "/about",
    name: "About",
    component: About,
    meta: { requiresAuth: false },
  },
  {
    path: "/login",
    name: "Login",
    component: Login,
    meta: { requiresAuth: false },
  },
  {
    path: "/information",
    name: "Information",
    component: Information,
    meta: { requiresAuth: true },
  },
  {
    path: "/message_board",
    name: "MessageBoard",
    component: MessageBoard,
    meta: { requiresAuth: true },
  },
  {
    // 路由比對失敗跳到主頁
    path: "/:pathMatch(.*)*",
    name: "Home",
    component: Home,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

router.beforeEach(async (to, from, next) => {
  if (to.meta.requiresAuth) {
    const user = localStorage.getItem(`user`);
    if (user) {
      const userdata = JSON.parse(user);
      let date = new Date().getTime();
      // 解密
      let decryptstoretime = CryptoJS.AES.decrypt(
        userdata[0].storetime,
        userdata[0].token
      );
      let storetime = decryptstoretime.toString(CryptoJS.enc.Utf8);
      if (date - Number(storetime) > 7 * 24 * 60 * 60 * 1000) {
        const store = useStore();
        if (firebaseauth.currentUser !== ``) {
          signOut(firebaseauth) //firebase登出帳號
            .then(() => {})
            .catch(() => {
              console.log("firebase登出失敗");
            });
        }
        axios({
          method: "post",
          url: store.state.api.GoogleAPI,
          params: {
            email: userdata[0].email,
            task: "4",
          },
        })
          .then((reponse) => {
            if (reponse.status === 200) {
              let api_reponse = reponse.data;
              if (api_reponse[0]["status"] == "0") {
                store.dispatch("auth/SignoutTask");
              } else if (api_reponse[0]["status"] == "1") {
                console.log("登出失敗");
              }
            } else {
              console.log("帳號操作失敗");
            }
          })
          .catch(() => {
            console.log("api失敗");
          });
        localStorage.removeItem(`user`);
        setTimeout(() => {
          next({ name: "Login" });
        }, 3000);
      } else {
        if (userdata[0].token.length > 0 && userdata[0].token !== undefined) {
          next();
        } else {
          next({ name: "Login" });
        }
      }
    } else {
      next({ name: "Login" });
    }
  } else {
    next();
  }
});

export default router;
