<template>
  <div
    class="container-fluid col-lg-4 col-md-8 m-auto mt-3 px-4"
    id="messagebox"
  >
    <!-- 留言暱稱 -->
    <div>
      <legend class="border border-2 border-danger bg-danger text-light">
        留言板
      </legend>
      <h5 class="text-start">你的回應</h5>
      <input
        type="text"
        class="form-control"
        maxlength="6"
        ref="refocus"
        v-model="nickname"
        spellcheck="false"
        placeholder="你的暱稱"
        autofocus
      />
    </div>
    <!-- 留言暱稱 -->
    <!-- 留言內容 -->
    <div class="mt-3 px-2 rounded" id="message-text">
      <hr class="mb-0 mx-2" />
      <textarea
        class="form-control border-0"
        :placeholder="placeholdertext"
        rows="3"
        spellcheck="false"
        maxlength="150"
        v-model="messagecontent"
        @input="TextareaInput"
      ></textarea>
      <hr class="mt-0 mb-2 mx-2" />
      <div class="d-inline-flex me-2 mb-1" id="word-count">
        <div class="me-2">Characters:</div>
        <div>{{ count }}/150</div>
      </div>
    </div>
    <!-- 留言內容 -->
    <!-- 送出按紐 -->
    <div class="mt-3 text-end">
      <button
        type="button"
        class="btn btn-primary rounded-pill col-3"
        @click="MessageSend"
      >
        送出
      </button>
    </div>
    <!-- 送出按紐 -->
    <!-- 分頁邏輯 -->
    <div v-if="totalpages > 0" class="mt-3 text-end">
      <ul class="pagination">
        <li class="page-item" @click="PrePage">
          <a
            class="page-link bg-danger text-light fw-bold"
            href="#"
            aria-label="Previous"
          >
            <span aria-hidden="true">上一頁</span>
          </a>
        </li>
        <li class="page-item">
          <a class="page-link" href="#">{{ nowpage + 1 }}</a>
        </li>
        <li class="page-item" @click="NextPage">
          <a
            class="page-link bg-danger text-light fw-bold"
            href="#"
            aria-label="Next"
          >
            <span aria-hidden="true">下一頁</span>
          </a>
        </li>
      </ul>
    </div>
    <!-- 分頁邏輯 -->
    <!-- 留言顯示 -->
    <div class="mt-3 mb-5" id="messages-show">
      <div
        class="col-lg-4 col-md-9 w-auto mb-3 border border-dark rounded-3"
        v-for="(message, index) in totalmessages[nowpage]"
        :key="message"
      >
        <div
          class="col-lg-3 col-md-8 w-auto mx-2 mt-2 pb-1 text-dark"
          id="messages-header"
        >
          <!--  ms-1 pt-2 -->
          <div class="fw-normal ms-1 pt-2" id="header-nick">
            {{ message.nickName }}
          </div>
          <!--  ms-0 me-2 pt-2 -->
          <div class="text-end ms-0 me-2 pt-2">
            <span class="me-2">#{{ index + 1 }}</span>
            <span class="text-nowrap">{{ message.inputTime }}</span>
          </div>
        </div>
        <div
          class="col-lg-3 col-md-8 w-auto mx-2 mt-2 pb-2 text-start text-break"
          id="message-content"
        >
          <p
            v-if="message.replayContent.length > 0"
            class="px-3 mb-2"
            id="message-replay-content"
          >
            回覆: {{ message.replayContent }}
          </p>
          <p class="mx-2 mt-0">{{ message.messageContent }}</p>
        </div>
        <div class="text-end col-lg-3 col-md-8 w-auto my-2 me-4">
          <span
            class="me-3 fw-bold"
            id="SpanReplay"
            @click="MessageReplay(index + nowpage * 5)"
            ><svg
              xmlns="http://www.w3.org/2000/svg"
              width="18"
              height="14"
              fill="currentColor"
              class="bi bi-arrow-90deg-left me-1"
              viewBox="0 0 16 16"
            >
              <path
                fill-rule="evenodd"
                d="M1.146 4.854a.5.5 0 0 1 0-.708l4-4a.5.5 0 1 1 .708.708L2.707 4H12.5A2.5 2.5 0 0 1 15 6.5v8a.5.5 0 0 1-1 0v-8A1.5 1.5 0 0 0 12.5 5H2.707l3.147 3.146a.5.5 0 1 1-.708.708l-4-4z"
              /></svg
            >回覆</span
          >
          <!-- MessageDelete(index + nowpage * 5) -->
          <span
            class="fw-bold"
            id="SpanDelete"
            @click="MessageDelete(message.nickName, message.inputTime)"
            ><svg
              xmlns="http://www.w3.org/2000/svg"
              width="18"
              height="18"
              fill="currentColor"
              class="bi bi-trash me-1"
              viewBox="0 0 16 16"
            >
              <path
                d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z"
              />
              <path
                fill-rule="evenodd"
                d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"
              /></svg
            >刪除</span
          >
        </div>
      </div>
    </div>
    <!-- 留言顯示 -->
  </div>
  <LoaadView v-if="loading" loadtext="載入中..." />
</template>

<script>
import Loading from "@/components/Loading.vue";
import axios from "axios";
import { useStore } from "vuex";
import { ref, toRefs, reactive, onMounted } from "vue";
// import moment from "moment";

export default {
  // 調用自身
  name: "MessageBoard",
  components: {
    LoaadView: Loading,
  },
  setup() {
    //vuex api
    const store = useStore();
    const refocus = ref(null); //焦距用，replay用到
    const data = reactive({
      placeholdertext: `請輸入你的回應`,
      count: 0,
      nickname: ``,
      messagecontent: ``,
      replaycontent: ``,
      messages: [],
      totalmessages: [], //所有messages數據
      handlemessages: [], //處理後的totalmessages數據
      pageitem: 5, //單頁顯示幾項
      totalpages: 0, //總計有幾頁，最少有1頁，用computed方式
      nowpage: 0, //當前顯示頁數
      loading: false,
    });
    //計算總頁數
    function CountPages() {
      data.totalpages = Math.ceil(data.messages.length / data.pageitem);
    }
    //留言的分頁邏輯處理
    function MessagePages() {
      CountPages();
      data.totalmessages = [];
      for (let i = 0; i < data.totalpages; i++) {
        data.totalmessages[i] = data.messages.slice(
          data.pageitem * i,
          data.pageitem * (i + 1) //slice(0,5),slice(5,10)，每次5筆資料
        );
      }
    }
    // 恢復原本設定
    function InitData() {
      data.nickname = ``; //匿名清空
      data.messagecontent = ``; //留言清空
      data.count = 0; //字數清零
    }
    // 字數
    function TextareaInput() {
      let wordcount = data.messagecontent.length;
      data.count = wordcount;
    }
    // 留言送出
    function MessageSend() {
      if (data.nickname === ``) {
        // eslint-disable-next-line no-undef
        Swal.fire({
          icon: "error",
          title: "請輸入暱稱!",
          confirmButtonText: "OK",
        });
        return;
      }
      if (data.messagecontent === ``) {
        // eslint-disable-next-line no-undef
        Swal.fire({
          icon: "error",
          title: "請輸入內容!",
          confirmButtonText: "OK",
        });
        return;
      }
      let messagesbox = {
        task: "8",
        work: "1",
        nickName: data.nickname,
        messageContent: data.messagecontent,
        replayContent: data.replaycontent,
      };
      data.loading = true;
      ShowData(messagesbox);
      InitData();
      data.replaycontent = ``; //回覆留言清空
      data.placeholdertext = `請輸入你的回應`; //placeholder屬性變回原樣
      MessagePages();
    }
    // 上一頁
    function PrePage() {
      if (data.nowpage > 0) {
        data.nowpage -= 1;
      }
    }
    // 下一頁
    function NextPage() {
      if (data.nowpage + 1 < data.totalpages) {
        data.nowpage += 1;
      }
    }
    // 留言回覆
    function MessageReplay(index) {
      self.scrollTo(0, 0);
      refocus.value.focus(); //自動焦距到匿名輸入
      data.replaycontent = data.messages[index].messageContent;
      data.placeholdertext = `回覆: ` + data.replaycontent;
      InitData();
    }
    // 留言刪除(index)
    function MessageDelete(nickName, inputTime) {
      // let messagesbox = {
      //   task: "8",
      //   work: "2",
      //   deleterow: index,
      // };
      let messagesbox = {
        task: "8",
        work: "2",
        nickName: nickName,
        inputTime: inputTime,
      };
      data.loading = true;
      ShowData(messagesbox);
      InitData();
      data.placeholdertext = `請輸入你的回應`; //placeholder屬性變回原樣
    }
    // 取得資料和儲存
    function ShowData(messagesbox) {
      axios({
        method: "post",
        url: store.state.api.GoogleAPI,
        params: messagesbox,
      })
        .then((response) => {
          if (response.status === 200) {
            data.messages = response.data;
            MessagePages();
            data.loading = false;
            if (data.nowpage + 1 > data.totalpages) {
              //確認當前頁數是否有數據
              PrePage();
            }
          } else {
            console.log("出錯啦");
          }
        })
        .catch((error) => {
          console.log(error);
        });
    }
    onMounted(() => {
      let messagesbox = {
        task: "8",
        work: "0",
      };
      data.loading = true;
      ShowData(messagesbox);
    });
    return {
      refocus,
      ...toRefs(data),
      CountPages,
      MessagePages,
      InitData,
      TextareaInput,
      MessageSend,
      PrePage,
      NextPage,
      MessageReplay,
      MessageDelete,
      ShowData,
    };
  },
};
</script>

