<template>
  <div class="container-fluid col-lg-4 col-md-12 mt-4 mx-auto">
    <div class="mx-1 border border-top-0 border-2 p-0">
      <!-- 藍色線 -->
      <div class="border-top border-primary border-5 mb-3">
        <div class="text-start fw-bolder fs-3 ms-3 mt-3">用戶編輯</div>
      </div>
      <hr />
      <div class="mt-2 mx-2 w-auto">
        <div class="text-start fs-5 fw-bold mb-2">暱稱</div>
        <div class="col-lg-8 col-md-10 text-start border mb-2 ps-2">
          {{ nickname }}
        </div>
        <div class="text-start fs-5 fw-bold mb-2">信箱</div>
        <div class="col-lg-8 col-md-10 text-start border mb-2 ps-2">
          {{ email }}
        </div>
        <div class="text-start fs-5 fw-bold">頭像</div>
        <div
          class="my-2 ms-5"
          data-bs-toggle="modal"
          data-bs-target="#cropper-modal"
          id="avatar-upload"
        >
          <img
            v-show="avatarshow"
            :src="avatarurl"
            id="avatar-show-img"
            alt=""
          />
          <svg
            v-show="!avatarshow"
            xmlns="http://www.w3.org/2000/svg"
            width="15"
            height="15"
            fill="currentColor"
            class="bi bi-person-circle"
            viewBox="0 0 16 16"
            id="avatar-show-svg"
          >
            <path d="M11 6a3 3 0 1 1-6 0 3 3 0 0 1 6 0z" />
            <path
              fill-rule="evenodd"
              d="M0 8a8 8 0 1 1 16 0A8 8 0 0 1 0 8zm8-7a7 7 0 0 0-5.468 11.37C3.242 11.226 4.805 10 8 10s4.757 1.225 5.468 2.37A7 7 0 0 0 8 1z"
            />
          </svg>
          <div id="hover-text">上傳頭像</div>
        </div>
      </div>
    </div>
    <div class="mt-4 mb-5">
      <button
        type="button"
        class="btn btn-success col-3 me-2"
        @click="AccountOperation('3')"
      >
        上傳
      </button>
      <button
        type="button"
        class="btn btn-secondary col-3 me-2"
        @click="SignOut"
      >
        登出
      </button>
      <button
        type="button"
        class="btn btn-danger col-4"
        @click="DeleteUserCheck"
      >
        刪除帳號
      </button>
    </div>
  </div>
  <!-- Modal -->
  <div
    class="modal fade"
    id="cropper-modal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div
      class="
        modal-dialog modal-xl modal-dialog-centered modal-dialog-scrollable
      "
    >
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title fs-5 fw-bold" id="exampleModalLabel">
            頭像裁剪
          </h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
            @click="ModalClose"
          ></button>
        </div>
        <div class="modal-body p-0">
          <div class="mx-3 mt-2">
            <input
              id="file-url"
              class="col-12"
              type="file"
              accept="image/*"
              @change="ChangeFile"
            />
          </div>
          <hr />
          <div class="row col-11 mx-auto">
            <!-- cropper框 -->
            <div class="col-lg-8 col-md-12" id="cropper-box">
              <div class="m-0 border" id="before-cropper" v-show="imageshow">
                剪裁區
              </div>
              <img
                :src="cropperurl"
                id="cropper-img"
                v-show="!imageshow"
                alt=""
              />
            </div>
            <!-- 裁剪預覽 -->
            <div class="col-lg-4 col-md-12">
              <div class="fw-bold fs-5 mt-2" id="preview-text">裁剪預覽</div>
              <div id="preview-box-round"></div>
            </div>
            <!-- 裁剪按鈕操作第一排 -->
            <div class="col-lg-10 col-md-12 mt-3 mx-auto" v-show="!imageshow">
              <button
                type="button"
                class="
                  btn btn-outline-secondary
                  fw-bolder
                  col-lg-3 col-md-2
                  me-2
                "
                @click="ZoomInImage"
              >
                放大
              </button>
              <button
                type="button"
                class="
                  btn btn-outline-secondary
                  fw-bolder
                  col-lg-3 col-md-2
                  me-2
                "
                @click="ZoomOutImage"
              >
                縮小
              </button>
              <button
                type="button"
                class="btn btn-outline-secondary fw-bolder col-lg-3 col-md-3"
                @click="RotateImage"
              >
                旋轉45°
              </button>
            </div>
            <!-- 裁剪按鈕操作第二排 -->
            <div
              class="col-lg-10 col-md-12 mt-2 mb-5 mx-auto"
              v-show="!imageshow"
            >
              <button
                type="button"
                class="
                  btn btn-outline-secondary
                  fw-bolder
                  col-lg-3 col-md-2
                  me-2
                "
                @click="ResetImage"
              >
                重置
              </button>
              <button
                type="button"
                :class="cropperlockclass"
                @click="LockCropperBox"
              >
                {{ cropperboxstatus }}
              </button>
              <button
                type="button"
                :class="cropperclearclass"
                @click="ClearCropper"
              >
                {{ cropperboxshow }}
              </button>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-secondary"
            data-bs-dismiss="modal"
            @click="ModalClose"
          >
            取消
          </button>
          <button
            type="button"
            class="btn btn-primary"
            @click="UploadImage"
            data-bs-dismiss="modal"
          >
            儲存
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { toRefs, reactive, onMounted } from "vue";
import { useRouter, onBeforeRouteLeave } from "vue-router";
import { useStore } from "vuex";
import axios from "axios";
import "cropperjs/dist/cropper.css";
import Cropper from "cropperjs"; //圖片剪裁
import CryptoJS from "crypto-js"; //加解密
import { firebasestorage, firebaseauth } from "@/config/firebasedb";
import {
  ref,
  uploadBytes,
  getDownloadURL,
  deleteObject,
} from "firebase/storage";
import { signInWithEmailAndPassword, signOut, deleteUser } from "firebase/auth";

export default {
  name: "Information",
  setup() {
    //api
    const store = useStore();
    //路由跳轉
    const router = useRouter();
    const data = reactive({
      avatarurl: ``, //頭像路徑
      cropperurl: ``, //剪裁框圖像路徑
      avatarshow: false, //確認有無頭像
      imageshow: true, //確認有無上要傳剪裁圖片
      cropper: ``, //放Cropper Object的
      cropperboxstatus: `鎖定`,
      cropperboxshow: `裁剪框消失`,
      cropperlockclass: `btn btn-outline-secondary fw-bolder col-lg-3 col-md-5 me-2`, //改變btn顏色
      cropperclearclass: `btn btn-outline-secondary fw-bolder col-lg-3 col-md-5 px-1`, //改變btn顏色
      nickname: ``,
      email: ``,
      userdata: [],
      imagename: ``, //上傳圖片網址
      uploadavatarcheck: true, //上傳圖片確認
    });
    //上傳圖片並生成裁剪框
    function ChangeFile(e) {
      const files = e.target.files || e.dataTransfer.files;
      const file = files[0];
      data.imageshow = false;
      data.cropperurl = window.URL.createObjectURL(file);
      // window.URL.revokeObjectURL(file); 用來換檔
      //另用種讀取檔案方法FileReader()
      // const file = e.target.files;
      // if (file) {
      //   let reader = new FileReader();
      //   reader.onload = (e) => {
      //     data.cropperurl = e.target.result;
      //   };
      //   reader.readAsDataURL(file[0]);
      // }
      const image = document.getElementById("cropper-img");
      data.cropper = new Cropper(image, {
        viewMode: 0, //0~3，只有0可以超出圖片
        aspectRatio: 1 / 1, //1:1縮放
        background: false,
        autoCropArea: 0.4, //裁剪框佔圖片10分之4
        dragMode: "none",
        minCropBoxWidth: 100,
        minCropBoxHeight: 100,
        minContainerWidth: 280,
        minContainerHeight: 280,
        scalable: false, //改變圖片尺寸的縮放
        preview: [document.querySelector("#preview-box-round")],
      });
      if (data.cropper) {
        data.cropper.replace(data.cropperurl); //取代空圖
      }
      // data.cropperurl = window.URL.revokeObjectURL(file);
    }
    // 放大圖片
    function ZoomInImage() {
      data.cropper.zoom(0.3);
    }
    // 縮小圖片
    function ZoomOutImage() {
      data.cropper.zoom(-0.3);
    }
    // 旋轉圖片
    function RotateImage() {
      data.cropper.rotate(45);
    }
    // 重置圖片
    function ResetImage() {
      if (data.cropperboxstatus == `解除鎖定`) {
        data.cropperlockclass = `btn btn-outline-secondary fw-bolder col-lg-3 col-md-5 me-2`;
        data.cropper.enable();
        data.cropperboxstatus = `鎖定`;
      }
      if (data.cropperboxshow == `裁剪框顯示`) {
        data.cropperclearclass = `btn btn-outline-secondary fw-bolder col-lg-3 col-md-5 px-1`;
        data.cropper.crop();
        data.cropperboxshow = `裁剪框消失`;
      }
      data.cropper.reset();
    }
    // 鎖定裁剪框
    function LockCropperBox() {
      if (data.cropperboxstatus == `鎖定`) {
        data.cropperlockclass = `btn btn-outline-danger fw-bolder col-lg-3 col-md-5 me-2`;
        data.cropper.disable();
        data.cropperboxstatus = `解除鎖定`;
      } else if (data.cropperboxstatus == `解除鎖定`) {
        data.cropperlockclass = `btn btn-outline-secondary fw-bolder col-lg-3 col-md-5 me-2`;
        data.cropper.enable();
        data.cropperboxstatus = `鎖定`;
      }
    }
    // 裁剪框消失
    function ClearCropper() {
      if (data.cropperboxstatus == `解除鎖定`) {
        data.cropperlockclass = `btn btn-outline-secondary fw-bolder col-lg-3 col-md-5 me-2`;
        data.cropper.enable();
      }
      if (data.cropperboxshow == `裁剪框消失`) {
        data.cropperclearclass = `btn btn-outline-danger fw-bolder col-lg-3 col-md-5 px-1`;
        data.cropper.clear();
        data.cropperboxshow = `裁剪框顯示`;
        data.cropper.disable();
        data.cropperboxstatus = `解除鎖定`;
      } else if (data.cropperboxshow == `裁剪框顯示`) {
        data.cropperclearclass = `btn btn-outline-secondary fw-bolder col-lg-3 col-md-5 px-1`;
        data.cropper.crop();
        data.cropperboxstatus = `鎖定`;
        data.cropperboxshow = `裁剪框消失`;
      }
    }
    // modal關閉
    function ModalClose() {
      const filename = document.getElementById("file-url");
      filename.value = ``; //清空input
      if (data.cropper !== ``) {
        data.cropper.destroy(); //清空裁剪區
      }
      data.imageshow = true;
    }
    // modal儲存
    function UploadImage() {
      if (!data.imageshow) {
        data.cropper
          .getCroppedCanvas({
            maxWidth: 4096,
            maxHeight: 4096,
            fillColor: `#fff`,
            imageSmoothingEnabled: true,
            imageSmoothingQuality: `high`,
          })
          .toBlob(
            (blob) => {
              // let reader = new FileReader();
              // reader.onload = (e) => {
              //   data.avatarurl = e.target.result;
              // };
              // reader.readAsDataURL(blob);
              data.avatarshow = true; //即時顯示
              uploadBytes(data.imagename, blob) //上傳頭像
                .then(() => {
                  getDownloadURL(data.imagename) //取得頭像網址
                    .then((response) => {
                      console.log(response);
                      data.avatarurl = response;
                      data.uploadavatarcheck = false;
                      ModalClose();
                    })
                    .catch(() => {
                      data.avatarshow = false;
                      console.log("圖片取得失敗");
                    });
                })
                .catch(() => {
                  data.avatarshow = false;
                  console.log("圖片上傳失敗");
                });
            },
            "image/jpeg",
            0.95
          );
      }
    }
    // Alert訊息
    function CallAlert(icontype, message) {
      // eslint-disable-next-line no-undef
      const Toast = Swal.mixin({
        toast: true,
        position: "center",
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true,
        didOpen: (toast) => {
          // eslint-disable-next-line no-undef
          toast.addEventListener("mouseenter", Swal.stopTimer);
          // eslint-disable-next-line no-undef
          toast.addEventListener("mouseleave", Swal.resumeTimer);
        },
      });

      Toast.fire({
        icon: icontype,
        title: message,
      });
    }
    // 帳號操作
    function AccountOperation(task) {
      let apiparams;
      if (task == "3") {
        apiparams = {
          email: data.email,
          avatar: data.avatarurl,
          task: task,
        };
        APIcall(apiparams);
      } else if (task == "4") {
        apiparams = {
          email: data.email,
          task: task,
        };
        APIcall(apiparams);
      } else if (task == "7") {
        apiparams = {
          email: data.email,
          task: task,
        };
        APIcall(apiparams);
      } else {
        console.log("指令錯誤");
      }
    }
    // API呼叫
    function APIcall(apiparams) {
      axios({
        method: "post",
        url: store.state.api.GoogleAPI,
        params: apiparams,
      })
        .then((responses) => {
          if (responses.status === 200) {
            let response = responses.data;
            if (response[0]["status"] == "0") {
              if (apiparams["task"] == "3") {
                data.uploadavatarcheck = true;
                CallAlert("success", "儲存成功");
                data.userdata = JSON.parse(localStorage.getItem(`user`));
                data.userdata[0].avatar = data.avatarurl;
                localStorage.setItem(`user`, JSON.stringify(data.userdata));
                store.dispatch("auth/LoginTask", data.userdata[0]);
              } else if (apiparams["task"] == "4") {
                store.dispatch("auth/SignoutTask");
              } else if (apiparams["task"] == "7") {
                store.dispatch("auth/SignoutTask");
                if (localStorage.getItem(`usermemory`)) {
                  localStorage.removeItem(`usermemory`);
                }
                localStorage.removeItem(`user`);
                router.replace({ name: "Login" });
              }
            } else if (response[0]["status"] == "1") {
              if (apiparams["task"] == "3") {
                CallAlert("error", "儲存失敗");
              } else if (apiparams["task"] == "4") {
                CallAlert("error", "登出失敗");
              } else if (apiparams["task"] == "7") {
                CallAlert("error", "帳號刪除失敗");
              }
            }
          } else {
            console.log("帳號操作失敗");
          }
        })
        .catch(() => {
          console.log("api失敗");
        });
    }
    // 登出帳號
    function SignOut() {
      let waittime = 100;
      if (data.uploadavatarcheck == false) {
        AccountOperation("3");
        waittime = 5500;
      }
      if (firebaseauth.currentUser !== ``) {
        signOut(firebaseauth) //登出帳號
          .then(() => {})
          .catch(() => {
            console.log("firebase登出失敗");
          });
      }
      AccountOperation("4");
      localStorage.removeItem(`user`);
      store.dispatch("auth/SignoutTask");
      setTimeout(() => {
        router.replace({ name: "Login" });
      }, waittime);
    }
    //刪除確認
    async function DeleteUserCheck() {
      // eslint-disable-next-line no-undef
      const { value: password } = await Swal.fire({
        title: "請輸入您的密碼",
        input: "password",
        inputPlaceholder: "例如:xxxxxx",
        inputAttributes: {
          minlength: 6,
          maxlength: 16,
          autocapitalize: "off",
          autocorrect: "off",
        },
        allowOutsideClick: false,
        showCancelButton: true,
        confirmButtonText: "確認",
        cancelButtonText: "取消",
      });

      if (password) {
        signInWithEmailAndPassword(firebaseauth, data.email, password) //登入帳號
          .then(() => {
            DeleteUser();
          })
          .catch(() => {
            // eslint-disable-next-line no-undef
            const Toast = Swal.mixin({
              toast: true,
              position: "center",
              showConfirmButton: false,
              timer: 2000,
              timerProgressBar: true,
              didOpen: (toast) => {
                // eslint-disable-next-line no-undef
                toast.addEventListener("mouseenter", Swal.stopTimer);
                // eslint-disable-next-line no-undef
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "error",
              title: "確認失敗",
            });
          });
      }
    }
    // 刪除帳號
    function DeleteUser() {
      data.uploadavatarcheck = true;
      deleteUser(firebaseauth.currentUser) //刪除帳號
        .then(() => {})
        .catch(() => {
          console.log("帳號刪除失敗");
        });
      deleteObject(data.imagename) //刪除頭像
        .then(() => {})
        .catch(() => {
          console.log("頭像刪除失敗");
        });
      AccountOperation("7");
    }
    onMounted(() => {
      let user = localStorage.getItem(`user`);
      if (user) {
        data.userdata = JSON.parse(user);
        store.dispatch("auth/LoginTask", data.userdata[0]);
        data.nickname = data.userdata[0].nickname;
        // 解密
        let decryptstoretime = CryptoJS.AES.decrypt(
          data.userdata[0].storetime,
          data.userdata[0].token
        );
        let storetime = decryptstoretime.toString(CryptoJS.enc.Utf8);
        let decryptSemail = CryptoJS.AES.decrypt(
          data.userdata[0].email,
          data.userdata[0].token
        );
        let decryptEemail = decryptSemail.toString(CryptoJS.enc.Utf8);
        let decryptemail = CryptoJS.AES.decrypt(decryptEemail, storetime);
        let email = decryptemail.toString(CryptoJS.enc.Utf8);
        //
        data.email = email;
        data.imagename = ref(
          firebasestorage,
          "images/" + data.email.replace(".", "_") + ".jpeg"
        );
        if (data.userdata[0].avatar != ``) {
          getDownloadURL(data.imagename) //取得頭像網址
            .then((response) => {
              if (data.userdata[0].avatar == response) {
                data.avatarurl = data.userdata[0].avatar;
                data.avatarshow = true;
              } else {
                // eslint-disable-next-line no-undef
                Swal.fire({
                  icon: "warning",
                  title: "頭像取得失敗，請重新登入或上傳",
                  text: `可能原因為:1.未上傳成功2.已從其它地方上傳頭像`,
                });
              }
            });
        }
      } else {
        router.replace({ name: "Login" });
      }
      if (data.uploadavatarcheck == false) {
        window.addEventListener("beforeunload", function (e) {
          e.returnValue = "無儲存";
        });
      }
    });
    onBeforeRouteLeave((to, from, next) => {
      if (data.uploadavatarcheck == false) {
        AccountOperation("3");
        next();
      }
      next();
    });
    return {
      ...toRefs(data),
      ChangeFile,
      ZoomInImage,
      ZoomOutImage,
      RotateImage,
      ResetImage,
      LockCropperBox,
      ClearCropper,
      ModalClose,
      UploadImage,
      CallAlert,
      AccountOperation,
      APIcall,
      SignOut,
      DeleteUserCheck,
      DeleteUser,
    };
  },
};
</script>

